function Home() {
  var classname = "HCE 01";
  var dssv = [
    "Nguyn Vn Cu Cng",
    "Trn Vn Ch Pho",
    "Nguyn Th N",
    "Trn Vn B Kin",
  ];

  var dssv2 = [
    { id: 1, hoten: "Nguyn Vn Cu Cng", lop: "k19" },
    { id: 2, hoten: "Trn Vn Ch Pho", lop: "k19" },
    { id: 3, hoten: "Nguyn Th N", lop: "k19" },
    { id: 4, hoten: "Trn Vn B Kin", lop: "k19" },
  ];

  return (
    <div>
      <div>
        <h1>TH BY NGY 27/09/2025</h1>
      </div>
      <div>Xin cho cc bn {classname}</div>
      <div>Danh sch sinh vin</div>
      <div>
        {dssv.map((sinhvien) => {
          return (
            <p>
              <span>{sinhvien}</span>
            </p>
          );
        })}
      </div>
      <div>
        <h2>Danh sch sinh vin 2</h2>
        <table>
          {dssv2.map((sinhvien) => {
            return (
              <tr>
                <td>id: {sinhvien.id}</td>
                <td>{sinhvien.hoten}</td>
                <td>{sinhvien.lop}</td>
              </tr>
            );
          })}
        </table>
      </div>
    </div>
  );
}

export default Home;
